import { useState } from 'react';
import { addToCart } from '../../utils/functions';
const Grid = ({ products, attributes }) => {
    const { columns } = attributes;
    const { desktop = 3, tablet = 2, mobile = 1 } = columns;
    const [loadingStates, setLoadingStates] = useState({});

    const setProductLoading = (productId, isLoading) => {
        setLoadingStates(prevState => ({
            ...prevState,
            [productId]: isLoading
        }));
    };

    return <div className={`grid columns-${desktop} columns-tablet-${tablet} columns-mobile-${mobile}`}>
        {
            products?.map((product, index) => {
                const { id, thumbnail, name, regular_price, price, sale_price, currency } = product;
                const isLoading = loadingStates[id] || false;
                return <div className='single_item' key={index}>
                    <div className="img">
                        <img src={thumbnail.src} alt="" />
                    </div>
                    <div className="name">
                        <h2>{name}</h2>
                    </div>

                    <div className="price_area">
                        {sale_price ? <div className="sale_price">
                            {sale_price} {currency}
                        </div> : <div className='discount_price'> <div className="regular_price">
                            {regular_price && <>{regular_price} {currency} </>}
                        </div>
                            <div className="price">
                                {price} {currency}
                            </div>
                        </div>}
                    </div>

                    <div className="cart_btn">
                        <button onClick={() => addToCart(id, 1, setProductLoading)}>
                            {isLoading ? 'Loading...' : 'Add to Cart'}
                        </button>
                    </div>
                </div>
            })
        }
    </div>
}
export default Grid;